@import url(https://fonts.googleapis.com/css?family=Amatic+SC|Indie+Flower|McLaren&display=swap);
@import url(https://fonts.googleapis.com/css?family=Amatic+SC|Indie+Flower&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: url(/images/polished-wood.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}
.Square {
    width: 12.5%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
}

.Square.white {
    background-color: white;
    background-image: url(/images/marble.jpg);
}

.Square.black {
    background-color: black;
    background-image: url(/images/dark-wood.jpg);
}

.Square img {
    width: 50%;
}
.Row {
    display: flex;
    flex-flow: row wrap;
    height: 12.5%;
}
#Board {
    padding-top: 55%;
    width: 55%;
    position: relative;
    box-shadow: 5px 10px 10px 3px rgba(0, 0, 0, 1);
    background-image: url(/images/frame.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
}

#Board #Content {
    position: absolute;
    top: 20px;
    left: 20px;
    right: 20px;
    bottom: 16px;
}

#Board .row {
    display: flex;
}

#Board .strip {
    position: absolute;
    display: flex;
}

#Board .strip#FileStrip {
    height: 25px;
    width: 95%;
    background-color: bisque;
    top: -25px;
    left: 2.5%;
    flex-flow: row;
    border-radius: 5px 5px 0 0;
    border: 2px solid darkgoldenrod;
    box-shadow: 5px 0px 10px 0px rgba(0, 0, 0, 0.5), 2px 2px 4px 0px rgba(0, 0, 0, 0.8) inset;
}

#Board .strip#FileStrip .File {
    width: 12.5%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    text-shadow: 1px 1px rgba(0, 0, 0, 0.1);
}

#Board .strip#RankStrip {
    height: 95%;
    width: 25px;
    background-color: bisque;
    top: 2.5%;
    left: -25px;
    flex-flow: column;
    border-radius: 5px 0 0 5px;
    border: 2px solid darkgoldenrod;
    box-shadow: 0px 5px 10px 0px rgba(0, 0, 0, 0.5), 2px 2px 4px 0px rgba(0, 0, 0, 0.8) inset;
}

#Board .strip#RankStrip .Rank {
    height: 12.5%;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    text-shadow: 1px 1px rgba(0, 0, 0, 0.1);
}
#App {
  margin: 50px 20px 0 50px;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
}

#Input {
    padding: 20px 10px 0 10px;
    display: flex;
    align-items: flex-start;
}

#Input textarea::-moz-selection {
    background-color: gold !important;
}

#Input textarea::selection {
    background-color: gold !important;
}

#Input textarea {
    background-color: transparent;
    border: none;
    font-size: 120%;
    color: rgba(255, 255, 255, 0.8);
    resize: none;
    height: 150px;
}

#Input textarea:focus {
    outline: none;
}

#Input input[type=submit] {
    background-color: gold;
    border: none;
    border-radius: 5px;
    padding: 10px 20px;
    cursor: pointer;
    font-size: 90%;
    box-shadow: 0 0 4px 0 rgba(0, 0, 0, 0.6);
    -webkit-transition: 50ms;
    transition: 50ms;
}

#Input input[type=submit]:active {
    background-color: rgba(51, 204, 255, 0.8);
}
#Buttons {
    display: flex;
    justify-content: space-around;
    margin-top: 30px;
    position: absolute;
    left: 0;
    bottom: 20px;
    width: 100%;
}

#Buttons button {
    background-color: gold;
    border: none;
    border-radius: 5px;
    padding: 10px 20px;
    cursor: pointer;
    font-size: 90%;
    box-shadow: 0 0 4px 0 rgba(0, 0, 0, 0.6);
    -webkit-transition: 50ms;
    transition: 50ms;
}

#Buttons button:active:not(#Buttons button:disabled) {
    background-color: rgba(51, 204, 255, 0.8);
}
#SessionNameInput {
    position: absolute;
    right: -100%;
    bottom: 0;
    width: 100%;
    border-top: 2px solid gold;
    border-bottom: 2px solid gold;
    box-shadow: 0 4px 8px 0px rgba(0, 0, 0, 0.7) inset;
    color: black;
    background-color: rgba(0, 0, 0, 1);
    background-image: url(/images/paper.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    font-family: 'McLaren', cursive;
    -webkit-transition: 500ms;
    transition: 500ms;
}

#SessionNameInput.visible {
    right: 0;
}

#SessionNameInput #SessionNameInputButtons {
    padding: 20px 20px 20px;
}

#SessionNameInput #SessionNameInputButtons .button {
    background-color: gold;
    border: none;
    border-radius: 5px;
    font-size: 100%;
    padding: 8px 10px;
    cursor: pointer;
    box-shadow: 0 0 4px 0 rgba(0, 0, 0, 0.6);
    -webkit-transition: 50ms;
    transition: 50ms;
}

#SessionNameInput #SessionNameInputButtons .button:active {
    background-color: rgba(51, 204, 255, 0.8);
}

#SessionNameInput form {
    padding: 20px;
}

#SessionNameInput form label {
    display: flex;
    flex-flow: column;
    margin-top: 30px;
    color: rgba(0, 0, 0, 0.5);
}

#SessionNameInput form label:first-child {
    margin-top: 10px;
}

#SessionNameInput form input[type=text]::-moz-selection, #SessionNameInput form textarea::-moz-selection {
    background-color: gold !important;
}

#SessionNameInput form input[type=text]::selection, #SessionNameInput form textarea::selection {
    background-color: gold !important;
}


#SessionNameInput form input[type=text], #SessionNameInput form textarea {
    background-color: transparent;
    border: none;
    border-bottom: 2px groove rgba(0, 0, 0, 0.4);
    font-size: 120%;
    color: rgba(0, 0, 0, 0.8);
}

#SessionNameInput form textarea {
    resize: none;
    height: 150px;
}

#SessionNameInput form #buttons {
    display: flex;
    justify-content: center;
    padding-top: 20px;
}

#SessionNameInput form textarea:focus, #SessionNameInput form input[type=text]:focus {
    outline: none;
}

#SessionNameInput form input[type=submit], #SessionNameInput form button {
    display: block;
    background-color: gold;
    border: none;
    border-radius: 5px;
    padding: 10px 40px;
    cursor: pointer;
    font-size: 90%;
    box-shadow: 0 0 4px 0 rgba(0, 0, 0, 0.6);
    -webkit-transition: 50ms;
    transition: 50ms;
}

#SessionNameInput form input[type=submit] {
    margin-right: 20px;
}

#SessionNameInput form button {
    padding: 10px 20px;
}

#SessionNameInput form input[type=submit]:active, #SessionNameInput form button:active {
    background-color: rgba(51, 204, 255, 0.8);
}

#SessionNameInput form #dbError {
    padding-top: 10px;
    color: red;
}
#SessionsPanel {
    position: absolute;
    right: -100%;
    bottom: 0;
    height: 90%;
    width: 100%;
    border-top: 2px solid gold;
    border-bottom: 2px solid gold;
    box-shadow: 0 4px 8px 0px rgba(0, 0, 0, 0.7) inset;
    color: black;
    background-color: rgba(0, 0, 0, 1);
    background-image: url(/images/paper.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    font-family: 'McLaren', cursive;
    -webkit-transition: 500ms;
    transition: 500ms;
}

#SessionsPanel.visible {
    right: 0;
}

#SessionsPanel #SessionsPanelButtons {
    padding: 20px 20px 0;
}

#SessionsPanel #SessionsPanelButtons .button {
    background-color: gold;
    border: none;
    border-radius: 5px;
    font-size: 100%;
    padding: 8px 10px;
    cursor: pointer;
    box-shadow: 0 0 4px 0 rgba(0, 0, 0, 0.6);
    -webkit-transition: 50ms;
    transition: 50ms;
}

#SessionsPanel #SessionsPanelButtons .button:active {
    background-color: rgba(51, 204, 255, 0.8);
}

#SessionsPanel #SessionsList {
    max-height: 80%;
    overflow: scroll;
    padding-top: 20px;
    border-top: 1px solid rgba;
}

#SessionsPanel .icon {
    background-color: gold;
    border: none;
    border-radius: 5px;
    font-size: 40%;
    width: 15px;
    padding: 6px 10px;
    cursor: pointer;
    box-shadow: 2px 2px 2px 0 rgba(0, 0, 0, 0.6);
    margin-right: 10px;
    -webkit-transition: 50ms;
    transition: 50ms;
}

#SessionsPanel .icon:active {
    background-color: rgba(51, 204, 255, 0.8);
}

#SessionsPanel #no-sessions {
    text-align: center;
    margin-top: 100px;
}


.Session {
    display: flex;
    align-items: center;
    padding: 10px 20px;
    font-size: 120%;
    border: 1px solid transparent;
    cursor: pointer;
    -webkit-transition: 150ms;
    transition: 150ms;
}

.Session:first-child {
    margin-top: 10px;
}

.Session .title {
    -webkit-transition: 250ms;
    transition: 250ms;
}

.Session:hover {
    border-top-color: rgba(0, 0, 0, 0.2);
    border-bottom-color: rgba(0, 0, 0, 0.2);
}

#SessionsPanel .Session .icon.danger {
    font-size: 50%;
    padding: 5.1px 12px;
    background-color: #ff8c1a;
}

#SessionsPanel .Session .icon:last-of-type {
    margin-right: 20px;
}

#SessionsPanel .Session .number-of-moves {
    opacity: 0.5;
    padding-left: 20px;
    font-size: 80%;
    position: relative;
    top: 2px;
}
#Panel {
    background-color: rgba(0, 0, 0, 0.7);
    box-shadow: 0 4px 8px 2px rgba(0, 0, 0, 1), 0 4px 8px 0px rgba(0, 0, 0, 0.7) inset;
    border: 2px solid gold;
    border-radius: 5px;
    background-image: url(/images/chalk-board-2.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    max-height: 700px;
    position: relative;
    overflow: hidden;
    font-family: 'McLaren', cursive;
}

#Panel #Error {
    color: orange;
    padding: 0 0 0 20px;
    font-weight: 300;
}

#Panel h3 {
    text-align: center;
    color: rgba(255, 255, 255, 1);
    padding: 0 20px;
    font-weight: 200;
}

#Panel hr {
    margin: -10px 20px 20px 20px;
    border: none;
    border-top: 1px solid rgba(255, 255, 255, 0.8);;
}

::-moz-selection {
    background-color: transparent;
}

::selection {
    background-color: transparent;
}
.Move {
    display: flex;
    align-items: center;
    color: rgba(255, 255, 255, 0.8);
    margin-bottom: 5px;
    padding: 10px 20px;
    cursor: pointer;
    -webkit-transition: 250ms;
    transition: 250ms;
}

.Move:hover {
    background-color: rgba(51, 204, 255, 0.2);
}

.Move.selected {
    background-color: gold;
    color: rgba(0, 0, 0, 1);
}

.Move.faded {
    opacity: 0.2;
}

.Move .TurnId {
    opacity: 0.5;
}

.Move .Side {
    border-radius: 50%;
    width: 10px;
    height: 10px;
    margin: 0 10px;
}

.Move .Side.Whites {
    background-color: white;
}

.Move .Side.Blacks {
    background-color: black;
}
#MovesHistory {
    margin-top: 10px;
    max-height: 53%;
    overflow: scroll;
}
